import Vue from 'vue';
import Vuex from 'vuex';
import firebase from '@/firebase';

Vue.use(Vuex);

const db = firebase.database;

export default new Vuex.Store({
  state: {
    groups: {},
    loading: false,
  },
  getters: {
    groups: state => state.groups,
    group: state => name => state.groups[name],
    loading: state => state.loading,
  },
  mutations: {
    addGroup(state, data) {
      Vue.set(state.groups, data.name, data.data);
    },
    setGroups(state, data) {
      state.groups = data; // eslint-disable-line no-param-reassign
    },
    setLoading(state, value) {
      state.loading = value; // eslint-disable-line no-param-reassign
    },
  },
  actions: {
    add(context, payload) {
      db.ref(`groups/${payload.name}/divider`).set(payload.divider);
      db.ref(`groups/${payload.name}/dateTaken`).set(payload.dateTaken);
      db.ref(`groups/${payload.name}/number`).set(payload.number);

      for (let i = 1; i <= payload.number; i++) { // eslint-disable-line no-plusplus
        const newItem = {
          id: i,
          o: false,
          x: false,
        };
        db.ref(`groups/${payload.name}/items`).push(newItem);
      }
    },
    update(context, payload) {
      db.ref(`groups/${payload.name}/items/${payload.key}`).update(payload.data);
    },
    updateTime(context, payload) {
      db.ref(`groups/${payload.name}/items/${payload.key}/n`).push(payload.data);
    },
    updateGroup(context, payload) {
      db.ref(`groups/${payload.name}`).set(payload.data);
    },
    deleteGroup(context, payload) {
      db.ref(`groups/${payload}`).remove();
    },
    getData(context) {
      context.commit('setLoading', true);
      try {
        db.ref('groups').on('value', (snapshot) => {
          context.commit('setGroups', snapshot.val());
          context.commit('setLoading', false);
        });
      } catch (error) {
        context.commit('setLoading', false);
        console.error(error); // eslint-disable-line no-console
      }
    },
  },
});
